html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

body {
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  overflow: hidden;
  overflow-y: scroll;
  min-width: 320px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
}

.inspiration__item-content, .inspiration__title, .about__content-title, .about__prev-title, .blog-box__title-item, .blog-box__title, .how-it-works__title, .decor__title, .new-collection__title, .top__slider-title, .rightside-menu__title, .footer_info-title, .menu__list-link {
  font-family: Montserrat;
  font-weight: 700;
}

.blog-box__date, .blog-box__author, .blog-box__theme, .gallery__menu-item, .footer__sotial-link, .footer__menu-link, .footer__form-button, .footer__info-email {
  font-family: Open Sans;
  font-weight: 600;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 110px;
}
.header__btn {
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
.header__btn-menu {
  display: none;
}

.menu {
  padding-right: 35px;
}
.menu__list {
  display: flex;
}
.menu__list-item {
  padding-right: 15px;
  padding-left: 35px;
  cursor: pointer;
}
.menu__list-link {
  line-height: 22px;
  color: rgba(54, 56, 56, 0.9);
  padding-bottom: 4px;
  position: relative;
}
.menu__list-link--active::after {
  content: "";
  background-color: rgb(54, 56, 56);
  height: 2px;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.logo {
  margin: 0 auto;
}

.footer__copyright {
  background-color: rgb(54, 56, 56);
  color: rgb(161, 161, 161);
  text-align: center;
  padding: 42px 15px 41px;
}
.footer__content {
  background-color: rgb(223, 223, 223);
  padding: 60px 0 80px;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
  padding-right: 60px;
}
.footer_info-title {
  padding-bottom: 20px;
}
.footer_info-text {
  width: 476px;
  padding-bottom: 20px;
  color: rgb(90, 90, 90);
}
.footer__info-email {
  display: inline-block;
  font-size: 14px;
  color: rgb(54, 56, 56);
  padding: 0 0 25px 25px;
  position: relative;
}
.footer__info-email::before {
  content: "";
  background-image: url(../img/envelope.svg);
  position: absolute;
  top: 8px;
  left: 0;
  width: 16px;
  height: 12px;
}
.footer__form {
  width: 539px;
  display: flex;
}
.footer__form-input {
  width: 70%;
  padding: 6px 10px 6px 18px;
  margin-right: 10px;
  border: 1px solid rgb(56, 54, 54);
  border-radius: 21px;
  opacity: 70%;
  background: transparent;
}
.footer__form-button {
  width: 160px;
  height: 40px;
  border: 1px solid rgb(54, 56, 56);
  border-radius: 21px;
  padding: 6px 12px;
  background: rgb(54, 56, 56);
  color: rgb(255, 255, 240);
  cursor: pointer;
}
.footer__sotial {
  margin-right: 145px;
}
.footer__sotial-item, .footer__menu-item {
  margin-bottom: 20px;
}
.footer__sotial-link, .footer__menu-link {
  font-size: 14px;
  color: rgb(112, 112, 112);
}
.footer__sotial-link {
  padding-left: 25px;
  position: relative;
}
.footer__sotial-link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  width: 16px;
  height: 16px;
}
.footer__sotial-link--facebook::before {
  background-image: url(../img/facebook.svg);
}
.footer__sotial-link--instagram::before {
  background-image: url(../img/instagram.svg);
  top: 4px;
  width: 14px;
  height: 14px;
}
.footer__sotial-link--pinterest::before {
  background-image: url(../img/Pinterest.svg);
}
.footer__sotial-link--whatsapp::before {
  background-image: url(../img/whatsapp.svg);
  top: 4px;
  width: 14px;
  height: 14px;
}
.footer__sotial-link--youtube::before {
  background-image: url(../img/youtube.svg);
  top: 5px;
  width: 18px;
  height: 12px;
}

.rightside-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  max-width: 694px;
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: -2px 3px 17px 0px #000;
  transition: all 0.5s;
  z-index: 100;
}
.rightside-menu__close {
  margin: 41px 0 60px 47px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.rightside-menu__content {
  text-align: center;
  width: 483px;
  margin: 0 auto;
}
.rightside-menu__logo {
  display: inline-block;
  margin-bottom: 40px;
}
.rightside-menu__title {
  font-size: 36px;
  line-height: 51px;
  color: rgb(54, 56, 56);
  position: relative;
  margin-bottom: 17px;
  padding-bottom: 17px;
}
.rightside-menu__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 252px;
  margin: 0 auto;
  height: 1px;
  background-color: #979797;
}
.rightside-menu__text {
  margin-bottom: 75px;
}

.disable {
  transform: translateX(500%);
}

.top__inner {
  position: relative;
  padding-bottom: 0px;
}
.top__inner::after {
  position: absolute;
  content: "";
  background-image: url(../img/main_slider.jpg);
  width: 100%;
  height: 722px;
  top: 0;
  z-index: -2;
}
.top__slider {
  width: 1260px;
  height: 800px;
  overflow: hidden;
  position: relative;
  margin-left: 10%;
}
.top__slider-info {
  max-width: 645px;
  align-items: center;
  margin-left: -22px;
  z-index: -1;
}
.top__slider-item {
  position: absolute;
  max-width: 1140px;
  max-height: 800px;
  display: flex;
  align-items: center;
  opacity: 1;
  transition: all ease 1s;
}
.top__slider-img {
  max-width: 703px;
  max-height: 815px;
  object-fit: cover;
}
.top__slider-title {
  color: rgb(54, 56, 56);
  font-size: 110px;
  line-height: 135px;
  padding-bottom: 26px;
  margin-bottom: 26px;
  position: relative;
}
.top__slider-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 185px;
  height: 1px;
  background: #6D7278;
}
.top__slider-text {
  color: #5A5A5A;
  max-width: 585px;
}
.top__inner-switch {
  padding: 10px 10px 70px 10px;
  display: flex;
  justify-content: space-between;
  width: 100px;
  margin: 0 auto;
}
.top__inner-switch-left {
  width: 20px;
  height: 20px;
  border: solid 1px rgb(54, 56, 56);
  border-radius: 50%;
  cursor: pointer;
}
.top__inner-switch-left .left {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}
.top__inner-switch-left:hover {
  background-color: #e3dede;
}
.top__inner-switch-left:active {
  background-color: #393939;
}
.top__inner-switch-right {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: solid 1px rgb(54, 56, 56);
  border-radius: 50%;
  cursor: pointer;
}
.top__inner-switch-right .right {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}
.top__inner-switch-right:hover {
  background-color: #e3dede;
}
.top__inner-switch-right:active {
  background-color: #393939;
}

.opacity0 {
  opacity: 0;
}

.new-collection {
  padding-bottom: 40px;
}
.new-collection__title {
  text-align: center;
  font-size: 36px;
  line-height: 51px;
  color: rgb(54, 56, 56);
  position: relative;
  padding-bottom: 30px;
}
.new-collection__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 43%;
  width: 252px;
  height: 1px;
  background-color: rgb(151, 151, 151);
}
.new-collection__text {
  margin: 0 auto;
  padding-top: 25px;
  padding-bottom: 30px;
  width: 730px;
  text-align: center;
  color: rgb(90, 90, 90);
}

.collection {
  padding-bottom: 50px;
}
.collection__pictures {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
}
.collection__picture-card {
  width: 350px;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.collection__picture-card:hover .collection__picture-hover {
  opacity: 1;
}
.collection__picture-card:hover .collection__img {
  opacity: 0.7;
}
.collection__picture-hover {
  max-width: 350px;
  max-height: 350px;
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
}
.collection__hover-title {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  color: rgb(54, 56, 56);
  padding-bottom: 19px;
  text-align: center;
  position: relative;
}
.collection__hover-title::after {
  content: "";
  position: absolute;
  left: 7%;
  bottom: 0;
  width: 200px;
  height: 1px;
  background-color: rgb(109, 114, 120);
}
.collection__hover-text {
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 600;
  line-height: 28px;
  text-align: center;
  color: rgb(112, 112, 112);
  padding-top: 19px;
}

.decor {
  background-image: url(../img/decor.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 170px 0 155px;
}
.decor__title {
  color: rgb(54, 56, 56);
  font-size: 60px;
  line-height: 80px;
  width: 731px;
  position: relative;
  padding-bottom: 22px;
}
.decor__title::after {
  content: "";
  width: 200px;
  height: 1px;
  background: rgb(151, 151, 151);
  position: absolute;
  bottom: 0;
  left: 0;
}
.decor__text {
  color: rgb(90, 90, 90);
  width: 540px;
  padding-top: 21px;
}

.how-it-works {
  padding-top: 0px;
  padding-bottom: 30px;
}
.how-it-works__title {
  font-size: 36px;
  line-height: 51px;
  color: rgb(54, 56, 56);
  text-align: center;
  padding-bottom: 40px;
}
.how-it-works__item {
  width: 225px;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 30px;
}
.how-it-works__img {
  margin-right: 12px;
}
.how-it-works__top-box {
  display: flex;
  border-bottom: 2px solid rgb(54, 56, 56);
  position: relative;
}
.how-it-works__top-box::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: rgb(54, 56, 56);
  border: 1px solid rgb(54, 56, 56);
  border-radius: 50%;
  position: absolute;
  bottom: -6px;
  left: -8px;
}
.how-it-works__item--one {
  margin-right: 300px;
}
.how-it-works__item--two {
  position: relative;
}
.how-it-works__item--two::after {
  content: "";
  width: 2px;
  height: 20px;
  background-color: rgb(54, 56, 56);
  position: absolute;
  bottom: 0px;
}
.how-it-works__img--two {
  width: 38px;
  height: 34px;
}
.how-it-works__item--three {
  padding-top: 30px;
  margin-left: 300px;
  position: relative;
}
.how-it-works__item--three::before {
  content: "";
  width: 2px;
  height: 20px;
  background-color: rgb(54, 56, 56);
  position: absolute;
  top: 0px;
}
.how-it-works__img {
  margin-bottom: 15px;
}

.gallery {
  padding: 0px 0 80px 0;
}
.gallery__menu {
  padding-top: 80px;
  margin: 0 auto;
  max-width: 900px;
  display: flex;
  justify-content: space-between;
}
.gallery__menu-item {
  display: inline-block;
  color: rgb(143, 141, 141);
  padding: 6px 18px;
  border: 1px solid rgba(69, 70, 71, 0.5);
  border-radius: 20px;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.5s;
}
.gallery__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 30px;
  padding-top: 40px;
}
.gallery__img:first-child {
  grid-column: span 2;
  grid-row: span 2;
}
.gallery__img {
  object-fit: cover;
  width: 100%;
  max-height: 1150px;
  height: 100%;
}
.gallery.gallery-page .gallery__img {
  max-height: 1175px;
}
.gallery.gallery-page .gallery__img:nth-child(8) {
  grid-column: span 2;
  grid-row: span 2;
}

.active-gallery {
  background-color: rgb(255, 239, 53);
  color: rgb(54, 56, 56);
}

.disabled {
  display: none;
}

.opacity7 {
  opacity: 0;
  transition-duration: 0.7s;
}

.blog-box {
  padding: 80px 0 120px;
}
.blog-box__title {
  margin: 0 auto;
  width: 430px;
  font-size: 36px;
  line-height: 51px;
  text-align: center;
  color: inherit;
}
.blog-box__items {
  padding-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
}
.blog-box__img-link {
  display: inline-block;
  padding-bottom: 20px;
}
.blog-box__links {
  padding-bottom: 10px;
}
.blog-box__date, .blog-box__author, .blog-box__theme {
  color: rgb(112, 112, 112);
  font-size: 14px;
  line-height: 28px;
}
.blog-box__title-item {
  font-size: 24px;
  line-height: 38px;
  color: rgb(54, 56, 56);
}

.breadcrumbs {
  background-color: rgba(223, 223, 223, 0.5);
  padding: 13px 14px;
}
.breadcrumbs__list {
  display: flex;
}
.breadcrumbs__item::after {
  content: "/";
  margin-right: 4px;
}
.breadcrumbs__item:last-child::after {
  display: none;
}
.breadcrumbs__link {
  font-size: 14px;
  color: rgb(90, 90, 90);
}

.about__prev {
  padding: 160px 0;
  background-image: url(../img/about-bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  margin-top: -53px;
}
.about__prev-title {
  text-align: center;
  font-size: 60px;
  line-height: 80px;
  color: rgb(54, 56, 56);
}
.about__content {
  padding: 130px 0 40px;
}
.about__content-title {
  width: 822px;
  font-size: 36px;
  line-height: 60px;
  color: rgb(54, 56, 56);
  padding-bottom: 20px;
  position: relative;
}
.about__content-title::after {
  content: "";
  position: absolute;
  width: 200px;
  height: 2px;
  background-color: rgb(109, 114, 120);
  bottom: 0;
  left: 0;
}
.about__content-text {
  padding-top: 23px;
}
.about__content-text p {
  margin-left: 285px;
  margin-bottom: 30px;
  width: 825px;
}
.about__vedeo {
  min-height: 414px;
  background-image: url(../img/about-video.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about__btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
}

.trailer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  background: rgba(0, 0, 0, 0.95);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
}
.trailer__video {
  position: relative;
  max-width: 900px;
  outline: none;
}
.trailer__close {
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  max-width: 32px;
}

.inspiration {
  padding-top: 100px;
  background-image: url(../img/inspiration-bg.jpg);
  background-repeat: no-repeat;
  background-position: bottom;
}
.inspiration__inner {
  display: flex;
}
.inspiration__img {
  margin-left: 180px;
  margin-right: 80px;
  padding-bottom: 30px;
}
.inspiration__content {
  max-width: 825px;
}
.inspiration__title {
  font-size: 36px;
  line-height: 60px;
  color: rgb(54, 56, 56);
  padding-bottom: 10px;
  position: relative;
}
.inspiration__title::after {
  content: "";
  position: absolute;
  width: 200px;
  height: 2px;
  background-color: rgb(151, 151, 151);
  bottom: 0;
  left: 0;
}
.inspiration__text {
  color: rgb(90, 90, 90);
  padding-top: 23px;
  padding-bottom: 60px;
}
.inspiration__item {
  margin-bottom: 33px;
}
.inspiration__item-content {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid #fff;
  border-radius: 4px;
}
.inspiration__item-content {
  color: rgb(54, 56, 56);
  font-size: 16px;
  line-height: 23px;
  padding-bottom: 14px;
}
.inspiration__line {
  background-color: rgb(54, 56, 56);
  margin-top: -2px;
  height: 2px;
}

.partners {
  padding-bottom: 16px;
}
.partners__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 768px) {
  .header__btn {
    display: none;
  }
  .logo {
    margin: 0;
  }
  .menu {
    padding-right: 0;
  }
  .menu__list-item {
    padding-left: 0;
  }
  .menu__list-item + .menu__list-item {
    padding-right: 0;
  }
}
@media (max-width: 540px) {
  .logo {
    order: -1;
    position: relative;
    z-index: 21;
  }
  .header__inner {
    flex-wrap: wrap;
    padding-top: 22px;
  }
  .header__btn-menu {
    display: block;
    background-color: transparent;
    border: none;
    order: -1;
    position: relative;
    z-index: 21;
    cursor: pointer;
  }
  .header__btn-menu span {
    display: block;
    background-color: #000;
    width: 37px;
    height: 3px;
    border-radius: 4px;
    margin: 6px 0;
  }
  .menu {
    padding-right: 0;
    width: 100%;
    background-color: #fff;
    position: relative;
    z-index: 11;
    text-align: center;
    transform: translateY(-280px);
    transition: all 0.5s;
  }
  .menu--open {
    transform: translateY(0px);
  }
  .menu__list {
    display: block;
  }
  .menu__list-item {
    padding-right: 0;
  }
  .menu__list-link {
    display: block;
    padding: 15px 0;
  }
}